@import './fonts';

// 예약전화 : 052)264-1017
@mixin callout{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    max-height: 60px;
    border: 1px solid #4095A2;
    color: #307B86;
    font-family: "Pretendard-SemiBold";
    font-size: 18px;
    
    span { // 강조 표시
        font-family: "Pretendard-Bold"
    }
}   

//믹스인 사용법 : @include resuable-style;

// > 30만상
@mixin subheading {
    font-family: "Pretendard-SemiBold";
    color: #353535;
    font-size: 18px;
    margin: 0;
    
    &::before{
        content: "▶ ";
    }
}
